<script>
import axios from 'axios';
import dotenv from 'dotenv';

dotenv.config()

function leftPad(value) {
    if (value >= 10) {
        return value;
    }

    return `0${value}`;
}

let date = new Date();
let month = date.getMonth() + 1

let CLSG_DT = date.getFullYear() + '.' + leftPad(date.getMonth() + 1) + '.' + leftPad(date.getDate());

let PAN_NT_ST_DT = date.getFullYear() + '.' + leftPad(date.getMonth()) + '.' + leftPad(date.getDate());

export default {
  name: 'Start',
  // "state" of the component, for proper state management for multiple components
  // have a look at vuex
  data () {
    return {
      result: '',
      month: month,
      api_url: 'http://apis.data.go.kr/B552555/lhLeaseNoticeInfo1/lhLeaseNoticeInfo1',
      api_params: {
          'ServiceKey' : process.env.VUE_APP_API_KEY,
          'PG_SZ' : '50', 
          'PAGE' : '1', 
          'PAN_NM' : 'ÏÑúÏö∏',
          'PAN_SS' : 'Í≥µÍ≥†Ï§ë',
          'PAN_NT_ST_DT' : PAN_NT_ST_DT, 
          'CLSG_DT' : CLSG_DT
      }
    }
  },
  // Declaration of parameters passed through the parent element
  props: {
    title: String
  },
  // methods that can be called
  methods: {
    /**
    * Calls the yes/no API with axios and saves that into the data.
    */
    loadData: function() {
      axios.get(this.api_url, {params: this.api_params}) // returns a promise
        .then(response => {
          // axios returns a response object, which contains the requested data
          // can be accessed via response.data
          this.result = response.data[1]['dsList']
        }).catch(err => {
          alert('Error while trying to call the api: ' + err)
        })
    }, 
    sortBy: function(sortKey) {
      this.reverse = (this.sortKey == sortKey) ? ! this.reverse : false;

      this.sortKey = sortKey;
    },

    addUser: function() {
      this.users.push(this.newUser);
      this.newUser = {};
    }
  },
  // vue-hook that gets called once the component is mounted
  mounted() {
    this.loadData()
  }
}
</script>

